<template>
  <div>
    <el-container>
      <el-header>
        <img class="mlogo" src="https://www.markerhub.com/dist/images/logo/markerhub-logo.png" alt="">
      </el-header>
      <el-main>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="username">
            <el-input v-model="ruleForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="ruleForm.password"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
          <el-form-item>
            <el-link type="primary" href="/register"></el-link>
          </el-form-item>
          <el-form-item>
            <el-button @click="goHome"></el-button>
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      ruleForm: {
        username: "Mrblue", // 
        password: "984065220", // 
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 15, message: " 3  15 ", trigger: "blur" },
        ],
        password: [
          { required: true, message: "", trigger: "blur" } //  blur 
        ],
      },
      loading: false, //  loading 
    };
  },
  methods: {
    submitForm(formName) {
  this.$refs[formName].validate((valid) => {
    if (valid) {
      this.$axios.post("/login", this.ruleForm).then((res) => {
        if (res.data.code === 200 && res.headers["authorization"]) {
          const jwt = res.headers["authorization"];
          const userInfo = res.data.data; // userInfo  role
          console.log(":", userInfo);
          
          this.$store.commit("SET_TOKEN", jwt);
          this.$store.commit("SET_USERINFO", userInfo);

          // 
          if (userInfo && userInfo.role === 'admin') {
            this.$message.success("...");
            this.$router.push({ name: 'AdminDashboard' }); // 
          } else {
            this.$message.success("");
            this.$router.push({ name: 'UserPage' }); // 
          }
        } else {
          this.$message.error(res.data.msg || '');
        }
      }).catch(err => {
        console.error(":", err);
        const errorMsg = err.response?.data?.msg || err.message || '';
        this.$message.error(errorMsg);
      });
    } else {
      console.log("");
      return false;
    }
  });
},
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    goHome() {
      // 
      //  store 
      const userInfo = this.$store.getters.getUser;

        this.$router.push({ name: 'UserPage' }); // 
      
    },
  },
}; //  export default 
</script>

