<template>
  <div>
    <el-container>
      <el-header>
        <img class="mlogo" src="https://www.markerhub.com/dist/images/logo/markerhub-logo.png" alt="">
      </el-header>
      <el-main>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="username">
            <el-input v-model="ruleForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="ruleForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="ruleForm.email"></el-input>
          </el-form-item>
          
          <el-form-item label="" prop="role">
            <el-select v-model="ruleForm.role" placeholder="">
              <el-option label="" value="user"></el-option>
              <el-option label="" value="admin"></el-option>
              </el-select>
          </el-form-item>

          <el-form-item label="" prop="avatar"> <input type="file" @change="handleFileUpload">
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
          
          <el-form-item>
            <el-link type="primary" href="/login"></el-link>
          </el-form-item>
          <el-form-item>
            <el-button @click="goHome"></el-button>
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      ruleForm: {
        username: '',
        password: '',
        email: '',
        role: 'user', //  'user'
        avatar: null // 
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: ['blur', 'change'] } //  'change' 
        ],
        role: [ // 
          { required: true, message: '', trigger: 'change' }
        ]
      }
    };
  },
  methods: {
    handleFileUpload(event) {
      this.ruleForm.avatar = event.target.files[0];
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const formData = new FormData();
          formData.append("username", this.ruleForm.username);
          formData.append("password", this.ruleForm.password);
          formData.append("email", this.ruleForm.email);
          formData.append("role", this.ruleForm.role);

          if (this.ruleForm.avatar) {
            formData.append("avatar", this.ruleForm.avatar);
          }
          
          this.$axios.post('/register', formData, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          }).then(res => {
            if (res.data.code === 200) { //  code  200
                 this.$alert('', '', {
                    confirmButtonText: '',
                    callback: action => {
                        this.$router.push("/login")
                    }
                });
            } else {
                //  code !== 200  HTTP  2xx res.data.msg 
                this.$message.error(res.data.msg || '');
            }
          }).catch(err => {
            // Axios  catch  HTTP  4xx/5xx 
            const errorMsg = err.response?.data?.msg || err.message || '';
            this.$message.error(errorMsg);
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.ruleForm.avatar = null;
      this.ruleForm.role = 'user'; // 
      // 
      const fileInput = this.$el.querySelector('input[type="file"]');
      if (fileInput) {
        fileInput.value = '';
      }
    },
    goHome() {
      this.$router.push("/");
    }
  }
}
</script>

<style scoped>
.mlogo {
  height: 60%;
  margin-top: 10px;
}
.demo-ruleForm {
  max-width: 500px;
  margin: 0 auto;
}
</style>