<template>
  <div class="m-content">
    <h3>Mrblue</h3>
    <div class="block">
      <el-avatar :size="50" :src="avatarUrl"></el-avatar>
      <div>{{ user.username }}</div>
    </div>

    <div class="maction">
      <span>
        <el-link @click="navigateTo(userHomePageLink)" :underline="false"></el-link>
      </span>
      <el-divider direction="vertical"></el-divider>
      <span>
        <el-link type="success" @click="navigateTo('/blog/add')" :underline="false"></el-link>
      </span>

      <el-divider direction="vertical"></el-divider>
      <span v-if="!hasLogin">
        <el-link type="primary" @click="navigateTo('/login')" :underline="false"></el-link>
      </span>

      <span v-if="hasLogin">
        <el-link type="danger" @click="logout" :underline="false"></el-link>
      </span>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'; 

export default {
  name: "Header",
  computed: {
    ...mapGetters(['getUser', 'isAdmin']),
    
    user() {
      return this.getUser || { username: '', avatar: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png' };
    },
    hasLogin() {
      return !!this.$store.state.token && this.getUser && typeof this.getUser.id !== 'undefined';
    },
    avatarUrl() {
      const avatarPath = this.user.avatar;
      if (avatarPath && avatarPath.startsWith('/uploads/')) {
        return `http://localhost:8081${avatarPath}`; // 
      }
      return avatarPath || 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png';
    },
    userHomePageLink() {
      if (this.hasLogin && this.isAdmin) {
        return { name: 'AdminDashboard' }; //  { name: 'AdminDashboard' }
      }
      return { name: 'UserPage' }; //  { name: 'UserPage' }
    }
  },
  methods: {
    logout() {
      this.$axios.get("/logout").then(res => {
        this.$store.commit("REMOVE_INFO");
        this.$router.push("/login"); // 
      }).catch(error => {
        console.error(":", error);
        this.$store.commit("REMOVE_INFO");
        this.$router.push("/login");
      });
    },
    goToAdminDashboard() {
      this.$router.push({ name: 'AdminDashboard' });
    },
    navigateTo(path) { // 
      if (this.$route.path !== path && !(this.$route.name === path.name && JSON.stringify(this.$route.params) === JSON.stringify(path.params))) {
        // 
        if (typeof path === 'string') {
          this.$router.push(path).catch(err => {
            if (err.name !== 'NavigationDuplicated') {
              console.error(err);
            }
          });
        } 
        //  ( { name: 'RouteName' })
        else if (typeof path === 'object' && path !== null) {
           this.$router.push(path).catch(err => {
            if (err.name !== 'NavigationDuplicated') {
              console.error(err);
            }
          });
        }
      }
    }
  }
}
</script>

<style scoped>
.m-content {
  max-width: 960px;
  margin: 0 auto;
  text-align: center;
}
.maction {
  margin: 10px 0;
}
.el-link { /*  */
  cursor: pointer;
}
</style>
